body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Prompt', sans-serif;
}
.navbar {
    background: rgb(63,185,139);
  background: linear-gradient(90deg, rgba(63,185,139,1) 15%, rgba(43,196,138,1) 47%, rgba(33,154,108,1) 86%);
  border-bottom: 5px solid  white; 
  z-index: 200;
}
.form-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    margin-top: 60px;
}
.form-box {
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 0.3) 10px 10px 3.5px;
}
.logo {
    height: 100px;
    width: 100%;
    margin-bottom: 10px;
}
.list-data {
    margin-top: 110px;
    box-shadow: rgba(0, 0, 0, 0.3) 10px 10px 3.5px;
}
.name {
    margin-top: 110px;
}
